import React, { Component, PropTypes } from 'react';
import Section from '../../components/Section/Section';
import ColumnsLayout from '../../components/ColumnsLayout/ColumnsLayout';
import './ProgrammeSection.scss';

const programme = [
  {
    header: '28 kwietnia, pitek',
    appendix: 'lectures',
    entries: [
      { time: '14:00', name: 'pocztek zakwaterowania wmiejscu noclegu dlaosb zwykupionym pakietemK1' },
      { time: '16:00', name: 'otwarcie recepcji Kongresu wLiceum Pijarw przyul.Akacjowej5' },
      { time: '18:30', name: 'powitanie uczestnikw iotwarcie Kongresu' },
      { time: '19:00', name: 'kolacja' },
      { time: '20:00', name: 'Adoracja Najwitszego Sakramentu' },
    ],
    children: [
      (<em>a po niej czas na integracj (do ok. 23:00)</em>),
    ]
  },
  {
    header: '29 kwietnia, sobota',
    appendix: 'workshops',
    entries: [
      { time: '8:15', name: 'jutrznia' },
      { time: '8:45', name: 'niadanie' },
      { time: '9:30', name: 'konferencja I' },
      { time: '10:30', name: 'konwersatorium I' },
      { time: '11:45', name: 'Msza wita wkociele pw. Najwitszego Imienia Maryi przyul. Dzielskiego1 pod przewodnictwem o.Pedro Aguado, Generaa Zakonu Pijarw  mog wniej bra udzia rwnie osoby nieuczestniczce wKongresie' },
      { time: '13:00', name: 'obiad' },
      { time: '14:30', name: 'warsztaty (1. seria  zwyjtkiem warsztatu muzycznego)' },
      { time: '16:15', name: 'konferencja II' },
      { time: '17:15', name: 'konwersatorium II' },
      { time: '18:15', name: 'kolacja' },
      { time: '20:00', name: 'koncert zespou ElendeS' },
    ]
  },
  {
    header: '30 kwietnia, niedziela',
    appendix: 'discussions',
    entries: [
      { time: '9:00', name: 'niadanie' },
      { time: '9:30', name: 'modlitwa poranna' },
      { time: '10:00', name: 'konferencja III' },
      { time: '11:00', name: 'konwersatorium III' },
      { time: '12:00', name: 'warsztaty (2. seria)' },
      { time: '13:30', name: 'obiad (+ do pobrania prowiant na kolacj)' },
      { time: '16:00', name: 'Msza wita wkociele pw. Przemienienia Paskiego przyul. Pijarskiej 2' },
      { time: '17:00', name: 'czas wolny (zpropozycjami tras naspacer)' },
    ]
  },
  {
    header: '1 maja, poniedziaek',
    appendix: 'project',
    entries: [
      { time: '8:30', name: 'niadanie' },
      { time: '9:30', name: 'Msza wita wkociele pw. Matki Boej Ostrobramskiej przyul. Meissnera 20 (kaplica w. Jzefa Kalasancjusza)' },
      { time: '10:30', name: 'podsumowanie izakoczenie Kongresu wLiceum Pijarw, propozycje na przyszo' },
      { time: '12:00', name: 'obiad' },
    ]
  },
]

export default class Introduction extends Component {
  static propTypes = {
    zIndex: PropTypes.number,
  }

  getColumns() {
    return programme.map(this.renderDay);
  }

  renderDay = ({ header, entries, children, appendix }) => (
    <div className={`ProgrammeSection-column ProgrammeSection-card ProgrammeSection-card--${appendix}`}>
      <h3>{header}</h3>
      <p>
        {entries.map(this.renderEntry)}
        {children}
      </p>
    </div>
  )

  renderEntry = ({ time, name }) => (
    <div className="ProgrammeSection-entry">
      <span className="ProgrammeSection-time">{time}</span>{name}
    </div>
  )

  renderLectures = () => (
    <div className="ProgrammeSection-column ProgrammeSection-card ProgrammeSection-card--lectures">
      <h3>Wykady wiodce</h3>
      <ul>
        <li>Czym jest duchowo?</li>
        <li>Na czym si opiera duchowo kalasantyska?</li>
        <li>Jak ni y dzi?</li>
      </ul>
    </div>
  )

  renderWorkshops = () => (
    <div className="ProgrammeSection-column ProgrammeSection-card ProgrammeSection-card--workshops">
      <h3>Warsztaty (dwa do wyboru)</h3>
      <ul>
        <li>Medytacja  modlitwa mylna</li>
        <li>Modlitwa nieustajca wcodziennoci</li>
        <li>Pismo wite iOjcowie Kocioa</li>
        <li>Asceza  poznanie siebie</li>
        <li>Cnoty kalasantyskie  nieskoczona cierpliwo iinne</li>
        <li>Kalasancjusz wmuzyce</li>
        <li>Kalasancjusz wobrazach</li>
      </ul>
    </div>
  )

  renderOtherCards = () => (
    <div className="ProgrammeSection-column">
      {this.renderDiscussions()}
      {this.renderProject()}
    </div>
  )

  renderDiscussions = () => (
    <div className="ProgrammeSection-card ProgrammeSection-card--discussions">
      <p><strong>Konwersatoria</strong>, czyli czas na dzielenie si, dyskusje iwyjanienia</p>
    </div>
  )

  renderProject = () => (
    <div className="ProgrammeSection-card ProgrammeSection-card--project">
      <p><strong>Projekt Kongresowy</strong>  rozmowa zewitym</p>
    </div>
  )

  render() {
    return (
      <Section className="ProgrammeSection" zIndex={this.props.zIndex}>
        <div className="ProgrammeSection-board">
          <h1 className="ProgrammeSection-header">Program</h1>
          <ColumnsLayout columns={this.getColumns()} />
        </div>
      </Section>
    );
  }
}



